@import url(https://fonts.googleapis.com/css2?family=Assistant:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  width: 100%;
  background-color: rgb(237, 242, 245);
}

.main {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.title {
  width: 100%;
  margin-top: 4.2rem;
}

.title h2 {
  text-transform: uppercase;
  font-size: 32px;
  font-family: 'Assistant', sans-serif;
  letter-spacing: 1px;
  font-weight: 800;
  color: rgb(70, 67, 67);
}

.formdiv{
  margin-top: 1.5rem;
  height: 80px;
  display: flex;
  justify-content: center;
  width: 100%;
}

form{
  display: flex;
  justify-content: center;
}

.formdiv label{
  font-size: 19px;
  font-family: 'Open Sans', sans-serif;
  letter-spacing: 1px;
  margin-right: 8px;
  height: 40%;
  margin-top: 1px;
}

.formdiv input{
  width: 60px;
  height: 31px;
  padding: 0px 6px;
  border: none;
  font-size: 18px;
  border-radius: 4px;
}

.formdiv button{
  margin-left: 7px;
  height: 31px;
  width: 110px;
  text-transform: uppercase;
  font-size: 14px;
  border-radius: 4px;
  border: none;
  background-color: rgb(122, 167, 235);
}

.textdiv{
  width: 47%;
  height: auto;
  margin-top: 1.6rem;
  padding-bottom: 2rem;
}

.textdiv p{
  font-size: 15px;
  font-family: 'Open Sans', sans-serif;
  line-height: 1.7;
  color: rgb(74, 85, 102);
}

.textdiv p:nth-child(2){
  margin-top: 2rem;
}
.textdiv p:nth-child(3){
  margin-top: 2rem;
}

.textdiv p:nth-child(4){
  margin-top: 2rem;
}

.textdiv p:nth-child(5){
  margin-top: 2rem;
}

.textdiv p:nth-child(6){
  margin-top: 2rem;
}

.textdiv p:nth-child(7){
  margin-top: 2rem;
}

.textdiv p:nth-child(8){
  margin-top: 2rem;
}




